import { useState, useEffect } from "react";
import Tours from "./components/Tours";
import PageLoading from "./components/PageLoading";

function App() {
  const url = "https://course-api.com/react-tours-project";
  const [isLoading, setIsLoading] = useState(true);
  const [tours, setTours] = useState([]);

  // fetch tours data from API
  const fetchTours = async () => {
    // try and catch errors
    try {
      const response = await fetch(url);
      const tours = await response.json();

      setIsLoading(false);
      setTours(tours);
    } catch (error) {
      setIsLoading(isLoading);
      console.log(error);
    }
  };

  // use useEffect to render fetchTours function once
  useEffect(() => {
    fetchTours();
  }, []);

  // removing tours from the list
  const deleteTour = (id) => {
    const alteredTours = tours.filter((tour) => tour.id !== id);

    setTours(alteredTours);
  };

  // display loading text while tours data is being fetched
  if (isLoading) {
    return (
      <div className="wrapper">
        <h1 className="main-heading">Vacation Assistant</h1>
        <PageLoading />
      </div>
    );
  }

  // display text and call for action if all tours have been removed
  if (tours.length === 0) {
    return (
      <>
        <div className="wrapper">
          <div className="tours-box">
            <h1 className="sub-heading">No locations to display</h1>
            <button className="button" onClick={fetchTours}>
              Refresh List
            </button>
          </div>
        </div>
      </>
    );
  }

  return (
    <div className="wrapper">
      <h1 className="main-heading">Vacation Assistant</h1>
      <h3>
        Here's a curated list of the finest locations for your next vacation
      </h3>
      <div className="tours-box">
        <Tours tours={tours} deleteTour={deleteTour} />
      </div>
    </div>
  );
}

export default App;
