import Tour from "./Tour";

const Tours = ({ tours, deleteTour }) => {
  return (
    <main>
      {tours.map((tour) => {
        return (
          <Tour
            key={tour.id}
            {...tour}
            className="tours-box"
            tour={tour}
            deleteTour={deleteTour}
          />
        );
      })}
    </main>
  );
};

export default Tours;
